@import url("https://fonts.googleapis.com/css?family=Montserrat:700");
@import url("https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,400i,600,700");



body {
	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	height: 100vh;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


.App {
  font-family: sans-serif;
  text-align: center;
  color: white;
}

h1 {
}

.wrapper {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(5, auto);
}

.tile {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 10px;
  min-height: 40px;
  font-weight: bold;
  font-size: 20px;

  border: 4px black dashed;
  border-radius: 10px;

  cursor: pointer;
}

.tile--set {
  background: khaki;
  color: black !important;
}

canvas {
  position: fixed;
  top: 0;
  left: 0;

  overflow-y: hidden;
  overflow-x: hidden;

  height: 100vh;
  width: 100vw;
  margin: 0;

  pointer-events: none;
}


.bingo_back {
  height: 100vh;
  overflow: hidden;
  font-family: "Montserrat", sans-serif;
}

.alert {
  background-color: #29d2e4;
  border: 1px solid #29d2e4;
  color: #fff;
  padding: 15px 20px;
  border-radius: 10px;
}
.alert .alert-icon {
  float: left;
  margin-right: 15px;
}
.alert .alert-info {
  margin: 0 10px 0 0;
  text-transform: uppercase;
  font-weight: normal;
  font-size: 14px;
}
.alert .alert-container {
  position: relative;
  max-width: 960px;
  margin: 0 auto;
}
.alert .close-icon {
  float: right;
  color: #000;
  margin-top: 0;
  margin-left: 0;
  width: 21px;
  height: 21px;
  position: relative;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  text-indent: -999px;
  overflow: hidden;
  white-space: nowrap;
}
.alert .close-icon:before, .alert .close-icon:after {
  content: "";
  position: absolute;
  top: 8px;
  width: 15px;
  height: 2px;
  left: 0;
}
.alert .close-icon:before {
  background-color: #fff;
  transform: rotate(-45deg);
}
.alert .close-icon:after {
  background-color: #fff;
  transform: rotate(45deg);
}
